.intro {
  display: flex;
  min-height: 90vh;
}

.intro__flex {
  margin: auto;
  display: flex;
  flex-direction: column;
}

.intro__flex p {
  font-size: 1rem;
  max-width: 14.5rem;
  box-sizing: border-box;
  margin: auto;
  text-align: center;
}

.intro__flex .btn__corner {
  margin-top: 2.25rem;
}